// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import {Button, Drawer, Card, Carousel, Row, Col, Table, TableColumn, Popover, Progress, Form, Input, Main, Container, Header,
  Dropdown, DropdownItem, FormItem, Notification, DropdownMenu, Image, CarouselItem, Tag, Select, Option, DatePicker,
  Pagination, Loading, Collapse, CollapseItem} from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import 'element-ui/lib/theme-chalk/display.css'
import '../static/css/style.css'
import '../libs/ezuikit.js'
// var ElementUI = require('element-ui')
// console.log('this is element', ElementUI)

// Vue.use(ElementUI)
Vue.use(CollapseItem)
Vue.use(Collapse)
Vue.use(Loading)
Vue.use(Option)
Vue.use(DatePicker)
Vue.use(Pagination)
Vue.use(Button)
Vue.use(Drawer)
Vue.use(Carousel)
Vue.use(Card)
Vue.use(Row)
Vue.use(Col)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Tag)
Vue.use(Popover)
Vue.use(Progress)
Vue.use(Form)
Vue.use(Input)
Vue.use(Main)
Vue.use(Header)
Vue.use(Container)
Vue.use(DropdownItem)
Vue.use(Dropdown)
Vue.use(FormItem)
Vue.use(Notification)
Vue.use(DropdownMenu)
Vue.use(Image)
Vue.use(CarouselItem)
Vue.use(Select)
Vue.prototype.$axios = axios
Vue.prototype.$axios.defaults.withCredentials = true
Vue.prototype.urlHead = 'http://127.0.0.1:8000'
// Vue.prototype.urlHead = ''

router.beforeEach((to, from, next) => {
  console.log(from.name)
  console.log(to.name)
  axios.get(Vue.prototype.urlHead + '/api/user/check_login/')
    .then((e) => {
      if (e.data.code === 0) {
        if (to.name === 'Login') {
          next({
            path: 'overview'
          })
        } else {
          next()
        }
      } else {
        if (to.name === 'Login') {
          next()
        } else {
          next({
            path: 'login'
          })
        }
      }
    })
    .catch(() => {
      if (to.name === 'Login') {
        next()
      } else {
        next({
          path: 'login'
        })
      }
    })
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



